$main: #5c0431;

.activeOrders {
  font-family: "Quicksand", sans-serif !important;

  .row {
    .card {
      .card-body {
        .btn {
          background: none;
          border: $main 1px solid;
          color: $main;
          border-radius: 0;
        }
        .btn:hover {
          background: $main;
          color: white;
          border: $main 1px solid;
        }
      }
    }
  }
}



.noOrders {
    font-family: "Quicksand", sans-serif !important;
    background: url("./MediaSCSS/errorbg.jpg");
    background-size: 100% 100%;
    height: 50rem;
  
    div {
      position: absolute;
      left: 58%;
      top: 34rem;
      h2 {
        color: $main;
      }
      .btn {
        background: $main;
        border: 1px solid $main;
      }
    }
  }
  
.modal {
  font-family: "Quicksand", sans-serif !important;
  button {
    border-radius: 0;
  }
  .updateStatus{
    background: $main;
    border: $main 1px solid;
  }
}

@media (max-width:394px) {
    .activeHeading{
        margin-top: 3rem;
    }
}



